import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.map.js";
import "core-js/modules/es.json.stringify.js";
import "core-js/modules/esnext.json.parse.js";
export default {
  name: 'UploadHistory',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      historyList: [],
      viewMode: 'grid' // 'grid' or 'list'
    };
  },
  watch: {
    show(val) {
      if (val) {
        this.loadHistory();
      }
    }
  },
  computed: {
    groupedHistory() {
      const groups = {};
      this.historyList.forEach(item => {
        const date = new Date(item.time);
        const dateStr = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
        if (!groups[dateStr]) {
          groups[dateStr] = [];
        }
        groups[dateStr].push(item);
      });

      // Sort dates descending
      return Object.keys(groups).sort((a, b) => new Date(b) - new Date(a)).map(date => ({
        date,
        items: groups[date]
      }));
    }
  },
  mounted() {
    // Load view mode preference
    const savedMode = localStorage.getItem('historyViewMode');
    if (savedMode) {
      this.viewMode = savedMode;
    }
  },
  methods: {
    loadHistory() {
      try {
        const history = JSON.parse(localStorage.getItem('uploadHistory') || '[]');

        // Sort by time desc
        this.historyList = history.sort((a, b) => b.time - a.time);
      } catch (e) {
        console.error('Failed to load history', e);
        this.historyList = [];
      }
    },
    toggleViewMode() {
      this.viewMode = this.viewMode === 'grid' ? 'list' : 'grid';
      localStorage.setItem('historyViewMode', this.viewMode);
    },
    clearHistory() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.historyList = [];
        localStorage.removeItem('uploadHistory');
        this.$message.success('');
      }).catch(() => {});
    },
    deleteItem(item) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // Remove from list
        this.historyList = this.historyList.filter(i => i.time !== item.time);

        // Update localStorage
        try {
          const history = JSON.parse(localStorage.getItem('uploadHistory') || '[]');
          const newHistory = history.filter(i => i.time !== item.time);
          localStorage.setItem('uploadHistory', JSON.stringify(newHistory));
          this.$message.success('');
        } catch (e) {
          console.error('Failed to update history', e);
        }
      }).catch(() => {});
    },
    isImage(fileName) {
      const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'tiff', 'ico', 'avif', 'heic', 'jfif', 'pjpeg', 'pjp'];
      const extension = fileName ? fileName.split('.').pop().toLowerCase() : '';
      return imageExtensions.includes(extension);
    },
    isVideo(fileName) {
      const videoExtensions = ['mp4', 'webm', 'ogg', 'mkv'];
      const extension = fileName ? fileName.split('.').pop().toLowerCase() : '';
      return videoExtensions.includes(extension);
    },
    formatTime(timestamp) {
      const date = new Date(timestamp);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },
    copyLink(url) {
      navigator.clipboard.writeText(url).then(() => {
        this.$message.success('');
      }).catch(() => {
        this.$message.error('');
      });
    },
    openLink(url) {
      window.open(url, '_blank');
    },
    handleImageError(e) {
      e.target.src = require('@/assets/404.png');
    }
  }
};